body {
    padding: 0;
    margin: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #1d1c22;
}

.search {

    margin-left: 50px;
    margin-bottom: 10px;
}

.search input {
    padding: 5px 10px;
    border-radius: 10px;

    outline: none;
    border: 1px solid gray;
    text-align: justify;
}

.card {
    background-color: aliceblue;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 1rem;

    width: 300px;
    height: 400px;
    z-index: 100;
    border-radius: 1rem;

    overflow: hidden;
    text-align: center;
    position: relative;

}

.wave {
    border: none;
    position: absolute;
    
    top: 50%;
    left: -15%;
    border-radius: 48%;
    width: 400px;
    height: 400px;
    animation: rotate 4.2s linear infinite;
    z-index: -1;
}

.wave:nth-child(1) {
    animation: rotate 4.1s linear infinite reverse;
    background-color: rgb(255 255 255 / 11%);
    top: 20%;
    z-index: 10;
}

.wave:nth-child(2) {
    animation: rotate 4.1s linear infinite reverse;
    background-color: rgb(10 238 140 / 9%);
    top: 23%;
    z-index: 20;

}

.wave:nth-child(3) {
    top: 25%;
    animation: rotate 4.3s linear infinite;
    background-color: rgb(14 234 231 / 7%);
    z-index: 11;
}

@keyframes rotate {
    form {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}


.icon img {
    margin: -11px;
}

.des {
    font-family: 'EB Garamond', serif;

}

.round {
    border-radius: 70px;
    background-color: transparent;
    width: 136px;
    height: 115px;
    padding-top: 20px;
    position: relative;
    top: -30px;
    z-index: 100;
    background-color: #ffffffc2;
}

.temp {
    font-size: 1.4rem;
    font-family: 'DynaPuff', cursive;
    margin: 9px;
}

.temp-f {
    font-size: 1rem;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.date {
    font-size: 1.3rem;
    font-weight: bold;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

.time {
    font-size: 1.1rem;
    font-weight: bold;

}

.place {
    font-size: 1.3rem;
    font-weight: bolder;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}